<template>
  <div class="contents">

    <div class="columns">
      <div v-for="work in works" key="{{ work.id }}" >
        <div v-if="work.id <= 3">
            <WorkCard
              :id = "work.id"
              :name = "work.name"
              :description = "work.description"
              :url = "work.url"
              :path = "work.path"
            >
            </WorkCard>
          </div>
        </div>
    </div>

    <div class="columns">
      <div v-for="work in works" key="{{ work.id }}" >
        <div v-if="work.id >= 4 && work.id <= 6">
          <WorkCard
            :id = "work.id"
            :name = "work.name"
            :description = "work.description"
            :url = "work.url"
            :path = "work.path"
          >
          </WorkCard>
        </div>
      </div>
    </div>

    <div class="columns">
      <div v-for="work in works" key="{{ work.id }}" >
        <div v-if="work.id >= 7 && work.id <= 9">
          <WorkCard
            :id = "work.id"
            :name = "work.name"
            :description = "work.description"
            :url = "work.url"
            :path = "work.path"
          >
          </WorkCard>
        </div>
      </div>
    </div>

  </div>
</template>

<script>
import WorkCard from '@/components/WorkCard'
import axios from 'axios'

export default {
  components: {WorkCard},
  name: 'Work',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App',
      works: null,
    }
  },
  methods: {

  },
  created () {
    this.works = require('../assets/works')
  }

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>

</style>



